// vereine/page.tsx
"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import type { Verein } from "../../types/types";
import "../../css/components/vereine.css";
import ErrorPage from "../error"; // Importiere die ErrorPage-Komponente

export default function VereinePage() {
  const [vereine, setVereine] = useState<Verein[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  const fetchVereine = async () => {
    try {
      const res = await fetch("/api/vereine");
      if (!res.ok) throw new Error("Fehler beim Laden der Vereine");
      const data = (await res.json()) as { vereine: Verein[] };
      setVereine(data.vereine);
    } catch (err) {
      setError(err as Error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchVereine(); // Lade die Vereine beim Laden der Seite
  }, []);

  if (error) {
    return <ErrorPage error={error} reset={fetchVereine} />;
  }

  return (
    <div className="vereine-container">
      <h1>Alle Vereine</h1>
      {loading ? (
        <p>Lade Vereine...</p>
      ) : (
        <div className="vereine-list">
          {vereine.map((verein) => (
            <div key={verein.id} className="verein-card">
              <Link
                href={verein.instagramProfileUrl || "https://www.instagram.com"}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={`/${verein.avatarUrl || "img/default-avatar.png"}`}
                  alt={verein.name}
                  className="verein-avatar"
                />
              </Link>
              <Link
                href={verein.instagramProfileUrl || "https://www.instagram.com"}
                target="_blank"
                rel="noopener noreferrer"
              >
                <h4>{verein.name}</h4>
              </Link>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
